<template>
  <div class="home">
  	<div class="title">猫眼电影</div>
    <router-view class="content"/>
    <Footer class="footer" />
  </div>
</template>

<script>
	import Footer from "./footer"
	
	export default {
		components:{
			Footer
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.home{
		width: 100%;
		
	}
	.home .title{
		width:100%;
		height: 50.5px;
		background: #e54847;
		color: #fff;
		font-size: 26px;
		text-align: center;
		line-height: 50.5px;
		position: fixed;
		left:0;
		top:0;
		right: 0;
		z-index: 2;
	}
	.home .content{
		position: absolute;
		left: 0;
		right: 0;
		bottom: 50px;
		top:50.5px;
	}
	.footer{
		position: fixed;
		left:0;
		bottom: 0;
		right: 0;
	}
	
	
	/*底部导航*/
	
</style>



